html,
body {
  font-family: 'Mukta', sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
  box-sizing: border-box;
  min-width: 320px; }

body {
  position: relative;
  overflow-x: hidden;
  padding: 0;
  margin: 0; }

.section {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center; }

/* body {
background-color: map-get($colors, theme-blue);
}
*/
.wrapper {
  width: 320px;
  min-width: 320px;
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }
  @media (min-width: 576px) {
    .wrapper {
      width: 540px; } }
  @media (min-width: 768px) {
    .wrapper {
      width: 720px; } }
  @media (min-width: 992px) {
    .wrapper {
      width: 960px; } }
  @media (min-width: 1200px) {
    .wrapper {
      width: 1140px; } }

/*
Пользовательские стили
*/
.title {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%; }
  .title .titleWrap {
    display: flex;
    flex-direction: column;
    max-width: 504px;
    text-align: center; }
    @media (min-width: 768px) {
      .title .titleWrap {
        text-align: left; } }
    .title .titleWrap .title__description {
      text-transform: uppercase;
      font-size: 16px;
      color: #F89F21; }
    .title .titleWrap .title__slogan {
      font-weight: bold;
      font-size: 50px;
      margin: 0;
      color: #252525; }
      .title .titleWrap .title__slogan:after {
        display: block;
        margin: 40px 0;
        content: "";
        width: 100%;
        height: 1px;
        background: #F89F21;
        opacity: .5; }
        @media (min-width: 768px) {
          .title .titleWrap .title__slogan:after {
            width: 50px;
            margin: 20px 0 0 0;
            background: linear-gradient(to right, #F89F21 0%, #F89F21 70%, #FFF 70%, #FFF 85%, #F89F21 85%, #F89F21 100%);
            height: 1.5px;
            opacity: 1; } }

.services {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 50px 0; }

.servicesItem {
  width: 100%;
  min-width: 230px;
  min-height: 260px;
  display: flex;
  flex-direction: column;
  margin: 0 0 10px 0;
  background-color: #f10f1a10;
  border-radius: 5px; }
  @media (min-width: 576px) {
    .servicesItem {
      width: 50%;
      margin: 0;
      background-color: unset; } }
  @media (min-width: 768px) {
    .servicesItem {
      width: 41%; } }
  @media (min-width: 992px) {
    .servicesItem {
      width: 31%; } }
  @media (min-width: 1200px) {
    .servicesItem {
      width: 26%; } }

.logoContainer {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  min-height: 100px; }
  .logoContainer .logoContainer__logo {
    margin: 15px 30px 35px 30px;
    min-width: 50px;
    width: 50px;
    height: 50px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover; }
    @media (min-width: 576px) {
      .logoContainer .logoContainer__logo {
        margin: 15px 40px 35px 0; } }
    .logoContainer .logoContainer__logo:before {
      position: absolute;
      transform: translate(15%, 35%);
      content: "";
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background-color: #FEE7C8;
      z-index: -1; }
  .logoContainer .logoContainer__headline {
    color: #252525;
    font-size: 24px;
    margin: 0; }

.servicesItem__desc {
  color: #6E6E6E;
  text-align: center; }
  @media (min-width: 576px) {
    .servicesItem__desc {
      text-align: unset; } }

.wideBtnContainer {
  min-width: 197px;
  position: relative;
  display: flex;
  justify-content: center;
  width: 100%; }
  @media (min-width: 768px) {
    .wideBtnContainer {
      justify-content: flex-end;
      align-items: flex-end;
      width: auto; } }
  .wideBtnContainer .wideBtn .wideBtn__link {
    display: block;
    line-height: unset;
    padding: 10px 25px;
    font-weight: 800;
    background-color: #EE4212;
    color: #FFFFFF;
    text-decoration: none;
    border: 1px solid #EE4212;
    transition: 0.3s linear; }
    .wideBtnContainer .wideBtn .wideBtn__link:hover {
      background-color: #FFFFFF;
      color: #EE4212; }
    .wideBtnContainer .wideBtn .wideBtn__link:after {
      content: "\2192";
      font-size: 24px; }

.startBtn {
  margin: 10px 0;
  text-align: center; }
  @media (min-width: 576px) {
    .startBtn {
      margin: 0;
      text-align: unset; } }
  .startBtn .startBtn__link {
    border: 1px solid #6E6E6E;
    padding: 10px 44px;
    color: #6E6E6E;
    transition: .3s linear;
    font-weight: 600;
    font-size: 16px;
    letter-spacing: 1px; }
    .startBtn .startBtn__link:hover {
      background-color: #6E6E6E;
      color: #FFFFFF; }
      @media (min-width: 576px) {
        .startBtn .startBtn__link:hover {
          background-color: unset;
          color: #EE4212; }
          .startBtn .startBtn__link:hover:after {
            color: #EE4212;
            transform: translateX(5px); } }
    @media (min-width: 576px) {
      .startBtn .startBtn__link {
        margin: 0;
        padding: 0;
        border: none; } }
    .startBtn .startBtn__link:after {
      content: "\2192";
      font-size: 24px;
      font-weight: 800;
      display: inline-block;
      transition: transform .2s linear; }

